
import "./KordleNoticePopup.css"
import "./popup.css"
function KordleNoticePopup({isOpenNotice, handleCloseNoticePopup}) {
    
    const handleKeyDownWarningInput = (e, idx) => {
        
    switch (e.key) {
        case "ArrowDown":
          // Do something for "down arrow" key press.
          break;
        case "ArrowUp":
          // Do something for "up arrow" key press.
          break;
        case "ArrowLeft":
          // Do something for "left arrow" key press.
          break;
        case "ArrowRight":
          // Do something for "right arrow" key press.
          break;
        case "Enter":
            handleCloseNoticePopup()
          // Do something for "enter" or "return" key press.
          break;
        case " ":
          // Do something for "space" key press.
          break;
        case "Escape":
            handleCloseNoticePopup()
          // Do something for "esc" key press.
          break;
        default:
          return; // Quit when this doesn't handle the key event.
      }

    }

    return (
        <div  className="KordleNoticePopup" style={isOpenNotice ? {display:"block"} : {display:"none"}} onKeyDown={handleKeyDownWarningInput}>
            {isOpenNotice && <div className="dimmed" onKeyDown={handleKeyDownWarningInput} />}
            <dialog className="popup" open={isOpenNotice}>
                <h1>  /    .</h1>
                <p>/   / . </p>
                <p style={{color:"#468d6b", fontWeight:"900"}}>   ( strike)   </p> 
                <p style={{color:"#f1c232", fontWeight:"900"}}>     ( ball)  </p> 
                <p style={{color:"grey", fontWeight:"900"}}>( )    ( out)  </p> 
                <p> /       .</p> 

                <br />
                <div>
                    <h2></h2>
                    <h3>           .</h3>
                    <h3>E-Mail</h3> nuckly60@gmail.com 
                    <h3>KAKAO TALK</h3>  <a href="https://open.kakao.com/o/gdYCzqOg"  target='_blank'></a>
                    <br />

                </div>
                <p>  ! </p>
                <p> </p>

                <button className="noticeButton" onClick={handleCloseNoticePopup}></button>
                
            </dialog>

        </div>
       
    )
}

export default KordleNoticePopup;